body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.starter-template {
  padding: 3rem 1.5rem;
}

h1 {
  margin-bottom: 50px;
  text-align: center;
}

button {
  min-width: 60px;
  min-height: 38px;
  outline: none;
  cursor: pointer;
}

button:hover,
button:active {
  opacity: 0.7;
}

.add {
  border-color: #28a745;
  background-color: #28a745;
}

.add:hover,
.add:active {
  border-color: #28a745;
  background-color: #28a745;
}

h2 {
  margin-bottom: 20px;
}

ul {
  list-style: none;
}

li {
  margin-bottom: 15px;
}

.col-lg-12 {
  padding: 0;
}

.delete {
  position: relative;
  border-color: #dc3545;
  background-color: #dc3545;
}

.delete:hover,
.delete:active {
  border-color: #dc3545;
  background-color: #dc3545;
}

.delete::before,
.delete::after {
  position: absolute;
  top: 9px;
  left: 28px;
  content: '';
  width: 2px;
  height: 20px;
  background-color: #ffffff;
}

.delete::before {
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}

.delete::after {
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.input-group-addon {
  border: 1px solid rgba(0,0,0,.15);
}

.form-control {
  -webkit-transition: all ease 0.2s;
  -o-transition: all ease 0.2s;
  transition: all ease 0.2s;
}

.form-control.active {
  border: 1px solid rgba(0,0,0,.15);
  border-right: none;
  background-color: #e9ecef;
}

li.col-lg-12 {
  margin-bottom: 20px;
}

.modal {
  display: block;
  background: rgba(0, 0, 0, .3);
}

.modal-dialog {
  top: calc(50% - 150px);
}

.modal-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.modal-header p {
  margin: 0;
}


